<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <div style="width:120px;float:left;margin-left:10px;">
            <template>
              <el-select v-model="value" placeholder="筛选">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </template>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()">搜索</el-button>
          </div>
          <div style="width:340px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTitle='新增信息(测试专用上线取消)'">新增信息</el-button>
            <el-button type="danger" @click="fn_repulse">批量拒绝</el-button>
            <el-button type="info" @click="fn_blacklist">批量拉黑</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="问题ID" width="80">
              </el-table-column>
              <el-table-column prop="userID" label="用户ID" width="80">
              </el-table-column>
              <el-table-column prop="userName" label="用户名" width="80">
              </el-table-column>
              <el-table-column label="是否匿名" width="80">
                <template slot-scope="props">
                  {{ props.row.isAnonymous == 1? "否" : "是" }}
                </template>
              </el-table-column>
              <el-table-column label="问题标题" width="300">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column label="问题详情">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="viewCount" label="查看数" width="80">
              </el-table-column>
              <el-table-column prop="followCount" label="关注人数" width="80">
              </el-table-column>
              <el-table-column prop="answerCount" label="回复数" width="80">
              </el-table-column>
              <el-table-column label="回复问题的用户ID" width="140">
                <template slot-scope="props">
                  <div :title='props.row.answerID' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.answerID}}</div>
                </template>
              </el-table-column>
              <el-table-column label="关注问题的用户ID" width="140">
                <template slot-scope="props">
                  <div :title='props.row.followID' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.followID}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="问题创建时间" width="120">
              </el-table-column>
              <el-table-column label="问题状态" width="80">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else-if='(props.row.status==0)' size='small'>驳回</el-tag>
                  <el-tag type="info" v-else size='small'>未审核</el-tag>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="90" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary">审核</el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info">详情</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:150px;">问题ID：{{id}}</div>
            <div class="f-left-right20">
              <div class="f-left">用户ID：</div>
              <div class="f-left">
                <el-input v-model="userID" size="mini" :disabled="zhiDu" placeholder="请输入内容"></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">用户名：</div>
              <div class="f-left">
                <el-input v-model="userName" size="mini" :disabled="zhiDu" placeholder="请输入内容"></el-input>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left">是否匿名：</div>
              <div class="f-left">
                <el-radio v-model="isAnonymous" :disabled="zhiDu" label="0">是</el-radio>
                <el-radio v-model="isAnonymous" :disabled="zhiDu" label="1">否</el-radio>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">查看数：{{viewCount}}</div>
            <div class="f-left-right50">关注人数：{{followCount}}</div>
            <div class="f-left-right50">回复数：{{answerCount}}</div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              问题标题：
            </div>
            <div class="f-left" style="width:879px;">
              <el-input v-model="title" size="mini" :disabled="zhiDu" placeholder="请输入问题标题"></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              问题描述：
            </div>
            <el-input v-model="content" :disabled="zhiDu" type="textarea" :rows="3" size="mini" placeholder="请输入内容"></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              回复问题的用户ID：
            </div>
            <el-input v-model="answerID" type="textarea" :disabled="zhiDu" :rows="3" size="mini" placeholder="请输入内容"></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              关注问题的用户ID：
            </div>
            <el-input v-model="followID" type="textarea" :disabled="zhiDu" :rows="3" size="mini" placeholder="请输入内容"></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:50%;">
              <div class="f-left">
                问题创建时间：
              </div>
              <div class="f-left">
                <el-date-picker v-model="ndate" :disabled="zhiDu" align="right" type="date" placeholder="选择日期" size="mini">
                </el-date-picker>
              </div>
            </div>
            <div class="f-left" style="width:49%;">
              <div class="f-left">
                问题状态：
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="success" v-if='(status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else-if='(status==0)' size='small'>驳回</el-tag>
                  <el-tag type="info" v-else-if='(status==-1)' size='small'>未审核</el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="add">
            <el-button type="" @click="centerDialogVisible = false">取 消</el-button>
            <el-button v-if="add2" type="primary" @click="fn_add">确 定</el-button>
          </div>
          <div v-if="!add">
            <el-button type="" @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="info" @click="fn_black">拉 黑</el-button>
            <el-button type="danger" @click="fn_check_to(0)">拒 绝</el-button>
            <el-button type="primary" @click="fn_check_to(1)">通 过</el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      zhiDu: false, // 是否开启只读
      add: true,
      add2: true,
      dialogTitle: '新增信息',
      currentPage: 1, // 当前页
      maxPage: 100,   // 总页数
      pageArr: 10, // 显示行数
      initial: 20, // 初始显示行数
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: '通过的问题'
      }, {
        value: '1',
        label: '驳回的问题'
      }],
      value: '',
      id: '',  //问题id
      userID: '', //用户id
      userName: '', //用户名
      isAnonymous: 0, //是否匿名 0匿名  1不匿名
      title: '',  //标题
      content: '',  //内容详情
      viewCount: '', //查看人数
      followCount: '',  //关注人数
      answerCount: '', //回复人数
      answerID: '',  //回复问题id
      followID: '',  //关注问题的用户id
      ndate: '',  //创建时间
      status: 0  //问题状态  0 驳回  1通过
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.zhiDu = false
      this.add = true
      this.add2 = true
      this.id = ''  //问题id
      this.userID = '' //用户id
      this.userName = '' //用户名
      this.isAnonymous = '' //是否匿名 0匿名  1不匿名
      this.title = ''  //标题
      this.content = ''  //内容详情
      this.viewCount = '' //查看人数
      this.followCount = ''  //关注人数
      this.answerCount = '' //回复人数
      this.answerID = ''  //回复问题id
      this.followID = ''  //关注问题的用户id
      this.ndate = ''  //创建时间
      this.status = ''  //问题状态  0 驳回  1通过
    },
    fn_repulse() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将拒绝审核所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/questauditList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消拒绝'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量功能喔~！'
        });
      }
    },
    fn_blacklist() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将拉黑所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/backquestList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消拉黑'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量功能喔~！'
        });
      }
    },
    fn_check(data) {
      this.dialogTitle = '审核信息'
      this.centerDialogVisible = true
      this.add = false
      console.log(data)
      this.id = data.id //问题id
      this.userID = data.userID //用户id
      this.userName = data.userName //用户名
      this.isAnonymous = data.isAnonymous +'' //是否匿名 0匿名  1不匿名
      this.title = data.title  //标题
      this.content = data.content  //内容详情
      this.viewCount = data.viewCount //查看人数
      this.followCount = data.followCount  //关注人数
      this.answerCount = data.answerCount //回复人数
      this.answerID = data.answerID  //回复问题id
      this.followID = data.followID  //关注问题的用户id
      this.ndate = data.ndate  //创建时间
      this.status = data.status  //问题状态  0 驳回  1通过
    },
    fn_look(data) {
      this.zhiDu = true
      this.add2 = false
      this.dialogTitle = '查看信息'
      this.centerDialogVisible = true
      this.id = data.id //问题id
      this.userID = data.userID //用户id
      this.userName = data.userName //用户名
      this.isAnonymous = data.isAnonymous + '' //是否匿名 0匿名  1不匿名
      this.title = data.title  //标题
      this.content = data.content  //内容详情
      this.viewCount = data.viewCount //查看人数
      this.followCount = data.followCount  //关注人数
      this.answerCount = data.answerCount //回复人数
      this.answerID = data.answerID  //回复问题id
      this.followID = data.followID  //关注问题的用户id
      this.ndate = data.ndate  //创建时间
      this.status = data.status  //问题状态  0 驳回  1通过
    },
    fn_add() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        isAnonymous = this.isAnonymous,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/questAdd", {
        userName, // 用户名
        isAnonymous, // 是否匿名
        title, // 问题标题
        content, // 问题描述
        ndate // 时间
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/questAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_black() {
      var _this = this
      var id = this.id
      this.post("/json/backquest", {
        id
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_loadTableList() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var pageNum = this.currentPage, pageSize = this.pageArr, keyword = this.keywords, status = this.status
      this.post("/json/questList", {
        pageNum, //页码,
        pageSize,//每页显示的数据,
        keyword,//关键字,
        status//问题状态
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>




