<template>
  <el-container>
    <el-main>
      <div style='background-color:#fff;height:400px;padding:10px;'>
       <el-form ref="formData" :model="formData" label-width="80px" style='width:400px;margin:50px auto 0;' :rules = 'rules'> 
          <el-form-item label="原密码" prop = 'oldpass'>
            <el-input v-model="formData.oldpass" type="password"></el-input>
          </el-form-item>
          <el-form-item label="新密码" prop = 'npass'>
            <el-input v-model="formData.npass" type="password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop = 'confirmPass'>
            <el-input v-model="formData.confirmPass" type="password"></el-input>
          </el-form-item>
        </el-form>
        <div style='width:400px;text-align:right;margin:0 auto;'>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="save" :loading="saving">保 存</el-button>
          </span>
        </div>
      </div>
    </el-main>
  </el-container>
</template>
<script>
export default {
  data() {
    return {
      formData:{},
      saving:false,
      rules:{
        oldpass:[
          { required: true, message: '请输入原密码', trigger: 'blur' },
        ],
        npass:[
          { required: true, message: '请输入新密码', trigger: 'blur' },
        ],
        confirmPass:[
          { required: true, message: '请输入确认密码', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    save(){
      this.$refs.formData.validate((valid)=>{
        if(!valid) return;
        let username = sessionStorage.getItem('name');
        let params = {userName:username};
        Object.assign(params,this.formData);
        this.saving = true;
        this.post('/json/modifypassword',params,(data)=>{
            if(data && data.status == 1){
              this.$refs.formData.resetFields();
            }
            this.saving = false;
        });
      })
    }
  }
}
</script>


