<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:230px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTiele='新增信息'">新增信息</el-button>
            <el-button type="danger" @click="fn_deletes()">批量删除</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange"
            :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="权限ID" width="150">
              </el-table-column>
              <el-table-column prop="title" label="权限名称" width="150">
              </el-table-column>
              <el-table-column label="权限类型" width="100">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.powerType==0)' size='small'>普通</el-tag>
                  <el-tag type="warning" v-else-if='(props.row.powerType==1)' size='small'>企业</el-tag>
                  <el-tag  v-else-if='(props.row.powerType==2)' size='small'>附加</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="content" label="权限描述">
              </el-table-column>
              <el-table-column prop="ndate" width="200" label="创建时间">
              </el-table-column>
              <el-table-column label="备注">
                <template slot-scope="props">
                  <div :title='props.row.remark' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.remark}}</div>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="redact(props.row)" type="primary">编辑</el-button>
                  </div>
                  <div>
                    <el-button @click="fn_delete(props.row.id)" size="mini" type="danger">删除</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTiele" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="resetForm('ruleForm')"
      :close-on-click-modal='false'>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <div>
            <div class="w-clear">
              <div class="f-left" style="width:200px;margin-top:10px">权限ID：{{ruleForm.id}}</div>
              <div class="f-left-right50">
                <!-- <div style="float:left;">权限名称：</div> -->
                <div style="float:left;">
                  <!-- <el-input v-model="title" placeholder="请输入名称" size="mini"></el-input> -->
                  <el-form-item label="权限名称：" prop="title">
                    <el-input v-model="ruleForm.title" placeholder="请输入内容" size="mini"></el-input>
                  </el-form-item>
                </div>
              </div>
              <div class="f-left-right50" style="margin-top:10px;">
                <div class="f-left">权限类型：</div>
                <div class="f-left">
                  <el-radio v-model="ruleForm.powerType" label="0">普通</el-radio>
                  <el-radio v-model="ruleForm.powerType" label="1">企业</el-radio>
                  <el-radio v-model="ruleForm.powerType" label="2">附加</el-radio>
                </div>
              </div>
            </div>
            <div>
              权限描述：
            </div>
            <div class="w-clear mb15">
              <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="ruleForm.content">
              </el-input>
            </div>
            <div class="w-clear mb15">
              <div class="f-left">创建时间：</div>
              <div class="f-left">
                <el-date-picker v-model="ruleForm.ndate" align="right" type="date" placeholder="选择日期" size="mini" value-format="yyyy-MM-dd">
                </el-date-picker>
              </div>
            </div>
            <div>
              备注：
            </div>
            <div class="w-clear mb15">
              <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="ruleForm.remark">
              </el-input>
            </div>
          </div>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      dialogTiele: '新增信息',
      centerDialogVisible: false,
      currentPage: 1, // 当前页
      maxPage: 100,   // 总页数
      pageArr: 10, // 显示行数
      tableData: [],
      multipleSelection: [],
      ruleForm: {
        content: '',
        id: '',
        ndate: '',
        powerType: '0',
        remark: '',
        title: ''
      },
      rules: {
        title: [
          { required: true, message: '请选择用户类型', trigger: 'change' }
        ],
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('submit!');
          this.fn_add()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    redact(rows) {
      console.log(rows)
      this.ruleForm.dialogTiele = '编辑信息'
      this.ruleForm.content = rows.content
      this.ruleForm.id = rows.id
      this.ruleForm.ndate = rows.ndate
      this.ruleForm.powerType = rows.powerType + ''
      this.ruleForm.remark = rows.remark
      this.ruleForm.title = rows.title
      this.centerDialogVisible = true
    },
    fn_delete(id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var _this = this
        this.post("/json/power/del", {
          id
        }, (data) => {
          _this.fn_loadTableList()
        })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/power/dellist", {
            id
          }, (data) => {
            _this.fn_loadTableList()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
        });
      }
    },
    hide() {
      this.ruleForm.content = ''
      this.ruleForm.id = ''
      this.ruleForm.ndate = ''
      this.ruleForm.powerType = ''
      this.ruleForm.remark = ''
      this.ruleForm.title = ''
    },
    fn_loadTableList() {
      var _this = this
      var pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/powerList", {
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
      var arr = []
      this.post("/json/role/power", {
        pageNum: ''
      }, (data) => {
        if (data.status == 1) {
          for (var i = 0, y = data.data.length; i < y; i++) {
            arr.push(JSON.stringify(data.data[i]))
          }
          var str = ''
          for (var i = 0; i < arr.length; i++) {
            if (i < arr.length - 1) {
              str = str + arr[i] + '$'
            } else {
              str = str + arr[i]
            }

          }
          localStorage.limits = str
        }
      })
    },
    fn_add() {
      var _this = this
      // var content = this.content,
      //   id = this.id,
      //   ndate = this.ndate,
      //   powerType = this.powerType,
      //   remark = this.remark,
      //   title = this.title,
      // a = {
      //   content,
      //   id,
      //   ndate,
      //   powerType,
      //   remark,
      //   title
      // }
      this.post("/json/power/add", _this.ruleForm, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    }
  },
  mounted() {
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>




