<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:300px;float:left;"  @keyup.enter="search">
            <el-input v-model="params.keyword" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" :max-height='mainHeight-132'>
              <el-table-column prop="userID" label="用户ID"></el-table-column>
              <el-table-column label="用户类型">
                <template slot-scope="props">
                  {{ userType[props.row.userType]}}
                </template>
              </el-table-column>
              <el-table-column label="内容类型">
                <template slot-scope="props">
                  {{ props.row.type == 0? "回复" : "问题" }}
                </template>
              </el-table-column>
              <el-table-column prop="title" label="内容标题">
              </el-table-column>
              <el-table-column prop="ndate" label="创建时间">
              </el-table-column>
              <el-table-column prop="backCount" label="违规次数">
              </el-table-column>
              <el-table-column label="操作" width="100" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='text' @click='showDetail(props.row)'>查看详情</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="getListAjax" :current-page.sync="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize" layout="total, sizes, prev, pager, next" :total="params.total">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog title="查看详情" :visible.sync="dialogVisible" width="800px" top="30vh" style='text-align:left' :close-on-click-modal='false'>
        <el-form :model="formData" label-width="90px">
          <el-row>
            <el-col :span="9">
              <el-form-item label="用户ID：">
                <el-tag type="info" size='small'>{{formData.userID}}</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="5">
               <el-form-item label="用户类型：">
                <el-tag type="success" size='small'>{{userType[formData.userType]}}</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="内容类型：">
                <el-tag type="success" v-if='(formData.type==0)' size='small'>回复</el-tag>
                <el-tag type="success" v-else size='small'>问题</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="违规次数：">
               <el-tag type="danger" size='small'>{{formData.backCount}}</el-tag>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="9">
              <el-form-item label="创建时间：">
                <el-tag type="success" size='small'>{{formData.ndate}}</el-tag>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
               <el-form-item label="内容标题：" >
                <span>{{formData.title}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="内容详情：">
                <span>{{formData.content}}</span>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="batchTransfer" :loading="saving">转入白名单</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      dialogVisible: false,
      saving:false,
      tableData: [],
      params:{
        pageSize:10,
        pageNum:1,
        keyword:'',
        total:0
      },
      formData:{}
    }
  },
  methods: {
    search(){
      if(this.params.pageNum != 1){
        this.params.pageNum = 1;
      }
      this.getListAjax();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.params.pageNum = 1;
      this.getListAjax();
    },
    showDetail(row){
      if(!row) return;
      this.formData = row;
      this.dialogVisible = true;
    },
    batchTransfer(){
      if(!this.formData) return;
      this.saving = true;
      let params = {qid:this.formData.qid,type:this.formData.type,aid:this.formData.aid}
      this.post("/json/an_q_blackW",params,(data)=>{
          if(!!data && data.status){
            this.dialogVisible = false;
            this.getListAjax();
          }
          this.saving = false;
      },()=>{
          this.saving = false;
      });
    },
    getListAjax(){
      this.post("/json/backsubjectList",this.params,(data)=>{
        if(!!data.data){
          this.tableData = data.data;
          this.params.total = data.totalPage;
        }
      });
    }
  },
  created(){
      this.getListAjax();
  }
}
</script>


