<template>
    <el-container>
        <el-main>
            <div class="main-box">
                <div class="function-box">
                    <div style="width:120px;float:right;">
                        <!-- <el-button type="primary" @click="centerDialogVisible=true,dialogTitle='新增信息'">新增信息</el-button> -->
                        <!-- <el-button @click="fn_deletes()" type="danger">批量删除</el-button> -->
                    </div>
                </div>
                <div class="table">
                    <template>
                        <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
                            <el-table-column type="selection" width="55">
                            </el-table-column>
                            <el-table-column prop="id" label="角色ID" width="100">
                            </el-table-column>
                            <el-table-column prop="title" label="角色名称" width="200">
                            </el-table-column>
                            <el-table-column label="角色介绍" width="200">
                                <template slot-scope="props">
                                    {{props.row.introduce}}
                                </template>
                            </el-table-column>
                            <el-table-column label="权限划分" width="400">
                                <template slot-scope="props">
                                    <div :title='props.row.pid' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.pid}}</div>
                                </template>
                            </el-table-column>
                            <el-table-column prop="ndate" label="创建时间" width="160">
                            </el-table-column>
                            <el-table-column label="备注">
                                <template slot-scope="props">
                                    <div :title='props.row.remark' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.remark}}</div>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="120" class-name='noneborder'>
                                <template slot-scope="props">
                                    <div style="margin-bottom:10px;">
                                        <el-button size="mini" type="primary" @click="redact(props.row)">编辑</el-button>
                                    </div>
                                    <div>
                                        <el-button @click="fn_delete(props.row.id)" size="mini" type="danger">删除</el-button>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </template>
                </div>
                <div class="w-clear page-color">
                    <div class="f-right" style="">
                        <div class="block">

                            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
                            </el-pagination>

                        </div>
                    </div>
                </div>
            </div>
            <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide()" :close-on-click-modal='false'>
                <div>
                    <div class="w-clear">
                        <div class="f-left-right50" style="width:150px">
                            <span> 角色ID：{{id}}</span>
                        </div>
                        <div style="float:left; margin-right:80px;">
                            <div style="float:left;">
                                角色名称：
                            </div>
                            <div style="float:left;">
                                <el-input v-model="title" placeholder="请输入姓名" size="mini" :disabled="zhiDu"></el-input>
                            </div>
                        </div>
                        <div style="float:left; width:40%;">
                            <div style="float:left;">
                                角色介绍：
                            </div>
                            <div style="float:left; width:70%;">
                                <el-input v-model="introduce" placeholder="请输入信息" size="mini"></el-input>
                            </div>
                        </div>
                    </div>
                    <div>
                        权限：
                    </div>
                    <div class="w-clear mb15">
                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                        <div style="padding-top: 15px 0;"></div>
                        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                            <el-checkbox v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox>
                        </el-checkbox-group>
                    </div>
                    <div class="w-clear mb15">
                        <div style="float:left;">创建时间：</div>
                        <div style="float:left; width:30%;" class="block">
                            <el-date-picker v-model="ndate" align="right" type="date" placeholder="选择日期" size="mini" value-format="yyyy-MM-dd">
                            </el-date-picker>
                        </div>
                    </div>
                    <div class="w-clear mb15">
                        <div>备注：</div>
                        <div>
                            <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="remark">
                            </el-input>
                        </div>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="centerDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="fn_add">保 存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>

var arrID = []
var arrName = []
var arr = ''
if (sessionStorage.limits) {
    arr = sessionStorage.limits.split('$')
}

for (let i = 0; i < arr.length; i++) {
    arrID.push(JSON.parse(arr[i]).id)
    arrName.push(JSON.parse(arr[i]).title)
}
let cityOptions = arrName;
export default {
    props:{
    mainHeight: [Number,String],
    userType: Array
    },
    data() {
        return {
            Url: 'http://int.answer.updrv.com',
            zhiDu: false,
            centerDialogVisible: false,
            dialogTitle: '新增信息',
            currentPage: 1, // 当前页
            maxPage: 100,   // 总条数
            pageArr: 10, // 显示行数
            tableData: [],
            id: '',
            title: '',
            introduce: '',
            multipleSelection: [],
            checkAll: false,
            checkedCities: [],
            cities: cityOptions,
            isIndeterminate: false,
            ndate: '',
            remark: ''
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            console.log(key, keyPath)
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath)
        },
        openEditDialog(row) {
            console.log(row)
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`)
            this.pageArr = val
            this.fn_loadTableList()
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`)
            this.currentPage = val
            fn_loadTableList()
        },
        redact(rows) {
            this.dialogTitle = '编辑信息'
            if (rows.status == 0) {
                this.zhiDu = true
            }

            if (rows.pid.length != '') {
                var arr_ID = rows.pid.split(',')
                for (var i = 0; i < arr_ID.length; i++) {
                    this.checkedCities.push(arrName[arrID.indexOf(parseInt(arr_ID[i]))])
                }
                if (arr_ID.length >= arrID.length) {
                    this.checkAll = true
                } else if (arr_ID[0] == '') {
                    this.isIndeterminate = false
                } else {
                    this.isIndeterminate = true
                }
            }

            this.id = rows.id
            this.title = rows.title
            this.ndate = rows.ndate
            this.remark = rows.remark

            // this.textarea = rows.basicLimits
            this.centerDialogVisible = true
        },
        fn_delete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
            }).then(() => {
                var _this = this
                this.post("/json/role/del", {
                    id
                }, (data) => {
                    _this.fn_loadTableList()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        fn_deletes() {
            if (this.multipleSelection.length > 0) {
                this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            } else {
                this.$message({
                    type: 'info',
                    message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
                });
            }
        },
        hide() {
            // console.log(1)
            this.zhiDu = false
            this.checkAll = false
            this.checkedCities = []
            this.isIndeterminate = false
            this.id = ''
            this.title = ''
            this.ndate = ''
            this.remark = ''
            this.introduce = ''
        },
        fn_add() {
            var _this = this
            var id = this.id, title = this.title, remark = this.remark, ndate = this.ndate, introduce = this.introduce, pid = []
            for (var i = 0, y = this.checkedCities.length; i < y; i++) {
                pid.push(arrID[arrName.indexOf(this.checkedCities[i])])
            }
            if (pid[0] == undefined) {
                _this.$message({
                    type: 'warning',
                    message: '请赋予相关权限之后在提交!'
                })
                return
            }

            this.post("/json/role/add", {
                id,
                title,
                remark,
                pid: pid,
                ndate,
                introduce
            }, (data) => {
                _this.centerDialogVisible = false
                _this.fn_loadTableList()
            })
        },
        fn_loadTableList() {
            var _this = this
            var pageNum = this.currentPage
            this.post("/json/role/list", {
                pageNum
            }, (data) => {
                _this.tableData = data.data
                _this.maxPage = data.totalPage
            })
        }

    },
    mounted() {
        // console.log(1)
        window.vue = this
        this.fn_loadTableList()
        cityOptions = []
        arrID = []
        arrName = []
        var arr = sessionStorage.limits.split('$')
        for (let i = 0; i < arr.length; i++) {
            arrID.push(JSON.parse(arr[i]).id)
            arrName.push(JSON.parse(arr[i]).title)
        }
        cityOptions = arrName;
        this.cities = arrName;
    }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>




