<template>
  <el-aside width="200px">
    <el-menu :default-active="active" class="el-menu-vertical-demo"min-height="100%" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b" :unique-opened="true" :default-openeds="open">
      <el-submenu index="1" v-if='isAdmin == 1'>
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>用户管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('accountManage')" index="1-1">账号管理</el-menu-item>
          <el-menu-item @click="fn_href('newJuSe')" index="1-2">角色管理</el-menu-item>
          <el-menu-item @click="fn_href('newQuanXian')" index="1-3">权限管理</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2" v-if='isAdmin == 1'>
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>内容管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('allQuestion')" index="2-1">个人问题</el-menu-item>
          <el-menu-item @click="fn_href('allAnswer')" index="2-2">个人答主</el-menu-item>
          <el-menu-item @click="fn_href('enterpriseQuiz')" index="2-3">企业提问</el-menu-item>
          <el-menu-item @click="fn_href('specialtyAnswer')" index="2-4">专业答主</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3" v-if='isAdmin == 1'>
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>校验管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('sensitiveWordManage')" index="3-1">敏感词管理</el-menu-item>
          <el-menu-item @click="fn_href('sensitiveWordAudit')" index="3-2">敏感词审核</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="4" v-if='isAdmin == 1'>
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>黑&nbsp;&nbsp;名&nbsp;&nbsp;单</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('blacklistUser')" index="4-1">黑名单用户</el-menu-item>
          <el-menu-item @click="fn_href('blacklistContent')" index="4-2">黑名单内容</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="5" v-if="false">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>财务管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('generalize')" index="5-1">概览</el-menu-item>
          <el-menu-item @click="fn_href('enterpriseUserManage')" index="5-2">企业用户管理</el-menu-item>
          <el-menu-item @click="fn_href('costSUm')" index="5-3">费用核算</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="6">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>我&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;的</span>
        </template>
        <el-menu-item-group>
          <el-menu-item @click="fn_href('mytasklist')" index="6-1">我的任务清单</el-menu-item>
          <el-menu-item @click="fn_href('passwordedit')" index="6-2">修改密码</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </el-aside>
</template>
<script>
export default {
  props:{
    isAdmin:[Number,String]
  },
  data() {
    return {
      active: '',
      open: []
    }
  },
  methods: {
    fn_href(str) {
      this.$router.push(str)
    }
  },
  mounted() {
    var urlJson = {
      "accountManage": '1-1',
      "newJuSe": '1-2',
      "newQuanXian": '1-3',
      "allQuestion": '2-1',
      "allAnswer": '2-2',
      "enterpriseQuiz": '2-3',
      "specialtyAnswer": '2-4',
      "sensitiveWordManage": '3-1',
      "sensitiveWordAudit": '3-2',
      "blacklistUser": '4-1',
      "blacklistContent": '4-2',
      "generalize": '5-1',
      "enterpriseUserManage": '5-2',
      "costSUm": '5-3',
      "mytasklist":'6-1',
      "passwordedit":'6-2'
    }
    var active = window.location.href
    active = active.split('/')[(active.split('/')).length - 1]
    if (urlJson[active]) {
      var open = (urlJson[active]).split('-');
      if ((open.length == 2) || (open.length == 1)) {
        open = [open[0]]
      } else if (open.length == 3) {
        open = [open[0], open[0] + '-' + open[1]];
      }
      this.open = open
      this.active = urlJson[active]
    }
  }
}

</script>
<style lang="scss" scoped>
.w-nav-name {
  height: 62px!important;
  background: url('../assets/logo1.png') no-repeat;
  padding-left: 65px;
}

.a {
  width: 0px!important;
}

.w-hide {
  transition: left 0.6s ease;
  position: absolute;
  left: 201px;
  top: 15px;
}

.w-show {
  transition: left 0.6s ease;
  position: absolute;
  left: 1px;
  top: 15px;
}
</style>



