<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search">搜索</el-button>
          </div>
          <div style="width:230px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTitle='新增信息(测试专用上线取消)'">新增信息</el-button>
            <el-button type="danger" @click="fn_deletes">批量删除</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="回复ID" width="120">
              </el-table-column>
              <el-table-column prop="userID" label="专业答主ID" width="120">
              </el-table-column>
              <el-table-column prop="userName" label="专业答主姓名" width="140">
              </el-table-column>
              <el-table-column prop="qyID" label="关联企业ID" width="120">
              </el-table-column>
              <el-table-column prop="qID" label="关联的问题ID" width="120">
              </el-table-column>
              <el-table-column prop="title" label="问题标题" width="340">
              </el-table-column>
              <el-table-column label="回复内容">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column label="是否点赞" width="80">
                <template slot-scope="props">
                  {{ props.row.islike == 1? "已点赞" : "未点赞" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="回复时间" width="120">
              </el-table-column>
              <el-table-column label="回复状态" width="100">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else size='small'>驳回</el-tag>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="90" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary">审核</el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info">详情</el-button>
                  </div>
                </template>
              </el-table-column>

            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="initialize" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:190px;">回复ID：{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">专业答主ID：</div>
              <div class="f-left">
                <el-input type="text" v-model="userID" size="mini" placeholder="请输入内容" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">专业答主姓名：</div>
              <div class="f-left">
                <el-input type="text" v-model="userName" size="mini" placeholder="请输入内容" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right20" style="width:220px;">
              <div class="f-left">关联企业ID：</div>
              <div class="f-left" style="width:136px;">
                <el-input type="text" v-model="qyID" size="mini" placeholder="请输入内容" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right20" style="width:293px;">
              <div class="f-left">关联的问题ID：</div>
              <div class="f-left">
                <el-input type="text" v-model="qID" size="mini" placeholder="请输入内容" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">
                问题标题：
              </div>
              <div class="f-left" style="width:250px;">
                <el-input type="text" v-model="title" placeholder="请输入内容" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div>回复详情：</div>
            <div>
              <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="content" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              用户是否点赞：
            </div>
            <div class="f-left">
              <template>
                <el-radio v-model="islike" label="1" :disabled='zhiDu'>是</el-radio>
                <el-radio v-model="islike" label="0" :disabled='zhiDu'>否</el-radio>
              </template>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left">问题创建时间：</div>
              <div class="f-left">
                <div class="block">
                  <el-date-picker v-model="ndate" align="right" type="date" size="mini" placeholder="选择日期" :disabled='zhiDu'>
                  </el-date-picker>
                </div>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left">
                问题状态：
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="success" v-if='(status==1)' size='medium'>通过</el-tag>
                  <el-tag type="danger" v-else size='medium'>驳回</el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="lock">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button v-if="add2" type="primary" @click="fn_add">确 定</el-button>
          </div>
          <div v-if="!lock">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="info" @click="centerDialogVisible = false">拉 黑</el-button>
            <el-button type="danger" @click="centerDialogVisible = false">拒 绝</el-button>
            <el-button type="primary" @click="centerDialogVisible = false">通 过</el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      dialogTitle: '新增信息',
      add2: true,
      currentPage: 1, // 当前页
      maxPage: 100,   // 总条数
      pageArr: 10, // 显示行数
      centerDialogVisible: false,
      lock: true,
      zhiDu: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      formData: {},
      id: '', //回复id
      userID: '', //专业答主id
      userName: '',//专业答主姓名
      qyID: '',//关联企业id
      qID: '',//关联的问题id
      title: '', //关联的问题标题
      content: '',//回复内容
      islike: '',  //是否点赞  0 未点赞  1 点赞
      ndate: '',  //回复时间
      status: '1'  //回复状态  0 驳回 1通过
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    initialize() { //  关闭窗口后，初始化数据
      this.zhiDu = false
      this.lock = true
      this.add2 = true
      this.id = '' //回复id
      this.userID = '' //专业答主id
      this.userName = ''//专业答主姓名
      this.qyID = ''//关联企业id
      this.qID = ''//关联的问题id
      this.title = '' //关联的问题标题
      this.content = ''//回复内容
      this.islike = '1'  //是否点赞  0 未点赞  1 点赞
      this.ndate = ''  //回复时间
      this.status = '1'  //回复状态  0 驳回 1通过
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
        });
      }
    },
    fn_check(data) {
      this.dialogTitle = '审核信息'
      this.lock = false
      this.centerDialogVisible = true
      this.id = data.id //回复id
      this.userID = data.userID //专业答主id
      this.userName = data.userName//专业答主姓名
      this.qyID = data.qyID//关联企业id
      this.qID = data.qID//关联的问题id
      this.title = data.title //关联的问题标题
      this.content = data.content//回复内容
      this.islike = data.islike + '' //是否点赞  0 未点赞  1 点赞
      this.ndate = data.ndate  //回复时间
      this.status = data.status + ''  //回复状态  0 驳回 1通过
    },
    fn_look(data) {
      this.dialogTitle = '查看信息'
      this.lock = true
      this.add2 = false
      this.centerDialogVisible = true
      this.id = data.id //回复id
      this.userID = data.userID //专业答主id
      this.userName = data.userName//专业答主姓名
      this.qyID = data.qyID//关联企业id
      this.qID = data.qID//关联的问题id
      this.title = data.title //关联的问题标题
      this.content = data.content//回复内容
      this.islike = data.islike + '' //是否点赞  0 未点赞  1 点赞
      this.ndate = data.ndate  //回复时间
      this.status = data.status + ''  //回复状态  0 驳回 1通过
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/majorList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/majorAdd", {
        userName, // 用户名
        title, // 问题标题
        content, // 问题描述
        ndate // 时间
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/answerAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/dellist", {
            id
          }, (data) => {
            _this.fn_loadTableList()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
        });
      }
    }
  },
  mounted() {
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


