<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="用户ID/用户名/昵称/手机号/邮箱"></el-input>
          </div>

          <div style="width:200px;float:left;margin-left:10px;">
            <template>
              <el-select v-model="value" placeholder="筛选">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </template>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList">搜索</el-button>
          </div>
          <div style="width:360px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTiele='新增信息'">新增信息</el-button>
            <el-button type="danger" @click="fn_deletes()">批量删除</el-button>
            <el-button type="warning" @click="excel">导出表格</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="用户ID" width="80">
              </el-table-column>
              <el-table-column prop="userName" label="用户名" width="160">
              </el-table-column>
              <el-table-column prop="nickName" label="昵称" width="160">
              </el-table-column>
              <el-table-column prop="phone" label="手机号" width="160">
              </el-table-column>
              <el-table-column prop="email" label="邮箱" width="300">
              </el-table-column>
              <el-table-column label="用户类型" width="120">
                <template slot-scope="props">
                  {{userType[props.row.userType]}}
                </template>
              </el-table-column>
              <el-table-column label="是否答主" width="80">
                <template slot-scope="props">
                  {{ props.row.isAnswerer == 1? "是" : props.row.isAnswerer == 2 ? "申请中" : '否' }}
                </template>
              </el-table-column>
              <el-table-column label="答主身份状态审核" width="150">
                <template slot-scope="props">
                  {{ props.row.isAudit == 1? "已审核" : props.row.isAudit == 2 ? '已拒绝' : '待审核'}}
                </template>
              </el-table-column>
              <el-table-column label="账号状态" width="80">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.status==0)' size='small'>启用</el-tag>
                  <el-tag type="danger" v-else size='small'>禁用</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="addTime" label="注册时间" width="160">
              </el-table-column>
              <el-table-column label="备注">
                <template slot-scope="props">
                  <div :title='props.row.remark' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.remark}}</div>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="redact(props.row)" type="primary">编辑</el-button>
                  </div>
                  <div>
                    <el-button @click="fn_delete(props.row.id)" size="mini" type="danger">删除</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTiele" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="resetForm('ruleForm'),hide()" :close-on-click-modal='false'>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <div class="w-clear">
            <div class="f-left-right50" style="width:150px;margin-top:10px;">用户ID：{{ruleForm.id}}</div>
            <div class="f-left-right50">
              <el-form-item label="用户名：" prop="userName">
                <el-input v-model="ruleForm.userName" placeholder="请输入用户名" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50">
              <el-form-item label="昵称：" prop="nickName">
                <el-input v-model="ruleForm.nickName" placeholder="请输入昵称" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="用户类型：" prop="userType">
                <el-select v-model="ruleForm.userType" placeholder="请选择活动区域" size="mini">
                  <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="是否答主：" prop="isAnswerer">
                <el-radio-group v-model="ruleForm.isAnswerer">
                  <el-radio label="1">是</el-radio>
                  <el-radio label="0">否</el-radio>
                </el-radio-group>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="手机号：" prop="phone">
                <el-input v-model.number="ruleForm.phone" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="邮箱：" prop="email">
                <el-input v-model="ruleForm.email" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="公司名称：" prop="company">
                <el-input v-model="ruleForm.company" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="职务名称：" prop="job">
                <el-input v-model="ruleForm.job" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div>简介：</div>
            <div>
              <el-form-item label="" prop="details" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.details" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div>经历介绍：</div>
            <div>
              <el-form-item label="" prop="content" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.content" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="账号状态：" prop="status">
                <el-radio-group v-model="ruleForm.status">
                  <el-radio label="0">启用</el-radio>
                  <el-radio label="1">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="注册时间：" required>
                <el-form-item prop="ndate">
                  <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.ndate" style="width: 100%;" value-format="yyyy-mm-dd"></el-date-picker>
                </el-form-item>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div>备注：</div>
            <div>
              <el-form-item label="" prop="remark" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.remark" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
        </el-form>

        </div>
        <span slot="footer" class="dialog-footer">

          <div v-if="lock">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
          </div>
          <div v-if="!lock">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="fn_add">保 存</el-button>
            <el-button type="danger" @click="fn_check(2)">拒 绝</el-button>
            <el-button type="success" @click="fn_check(1)">通 过</el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      lock: true,
      dialogTiele: '新增信息',
      currentPage: 1, // 当前页
      maxPage: 100,   // 总条数
      pageArr: 10, // 显示行数
      tableData: [],
      newTableData: [],
      centerDialogVisible: false,
      multipleSelection: [],
      keywords: '',
      options: [
        {
          value: '0',
          label: '全部'
        },
        {
          value: '1',
          label: '企业用户'
        },
        {
          value: '2',
          label: '个人用户'
        },
        {
          value: '3',
          label: '账号状态（启用）'
        },
        {
          value: '4',
          label: '账户状态（禁用）'
        }
      ],
      options2: [
        {
          value: '0',
          label: '游客'
        },
        {
          value: '1',
          label: '个人用户'
        },
        {
          value: '2',
          label: '企业用户'
        },
        {
          value: '3',
          label: '个人答主'
        },
        {
          value: '4',
          label: '专业答主'
        },
        {
          value: '5',
          label: '管 理 员'
        },
        {
          value: '6',
          label: '超级管理员'
        },
      ],
      value: '', // 第一个select
      id: '',
      ruleForm: {
        userType: '',
        id: '',
        userName: '',
        nickName: '',
        phone: '',
        email: '',
        isAnswerer: '0',
        status: '0',
        ndate: '',
        content: '',
        company: '',
        job: '',
        remark: '',
        details: ''
      },
      rules: {
        userType: [
          { required: true, message: '请选择用户类型', trigger: 'change' }
        ],
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        nickName: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '请输入正确的手机号码', trigger: 'blur' },
          { pattern: /\d{11}/, message: '请输入有效手机号码', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { pattern: /^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/, message: '请输入有效邮箱', trigger: 'blur' }
        ],
        isAnswerer: [
          { required: true, message: '请选择一个性质', trigger: 'change' }
        ],
        status: [
          { required: true, message: '请选择一个状态', trigger: 'change' }
        ],
        ndate: [
          { required: true, message: '请选择日期', trigger: 'change' }
        ],
        content: [
          { required: true, message: '请填写相关经历介绍', trigger: 'blur' }
        ],
        company: [
          { required: true, message: '请填写公司名称', trigger: 'blur' }
        ],
        job: [
          { required: true, message: '请填写相关职务', trigger: 'blur' }
        ],
        remark: [
          { required: false, message: '', trigger: 'blur' }
        ],
        details: [
          { required: true, message: '请填写相关简介', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    openEditDialog(row) {
      console.log(row)
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.fn_add()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    redact(rows) {
      console.log(rows);
      this.dialogTiele = '编辑信息'
      this.lock = false
      this.ruleForm.id = rows.id
      this.ruleForm.userType = rows.userType + ''  // 弹窗select
      this.ruleForm.userName = rows.userName
      this.ruleForm.nickName = rows.nickName
      this.ruleForm.phone = rows.phone
      this.ruleForm.email = rows.email
      this.ruleForm.isAnswerer = rows.isAnswerer + ''
      this.ruleForm.status = rows.status + ''
      this.ruleForm.ndate = rows.addTime
      this.ruleForm.content = rows.content
      this.ruleForm.company = rows.company
      this.ruleForm.job = rows.job
      this.ruleForm.remark = rows.remark
      this.ruleForm.details = rows.details


      this.centerDialogVisible = true
    },
    fn_add() {
      this.post("/json/add", this.ruleForm, (data) => {
        if(data && data.status == 1){
          this.fn_loadTableList()
          this.centerDialogVisible = false
        }
        

      });
    },
    fn_delete(id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var _this = this

        this.post("/json/del", {
          id
        }, (data) => {
          _this.fn_loadTableList()

        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/user/dellist", {
            id
          }, (data) => {
            _this.fn_loadTableList()

          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
        });
      }
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr, status = this.value
      this.post("/json/userList", {
        pageNum, // 当前页
        pageSize, // 每页显示的条数
        status, // 状态  0 全部  1 企业用户  2 个人用户 3 账号启用 4 账号禁用
        keyword
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage

      });
    },
    hide() {
      this.lock = true
      this.dialogTiele = ''
      this.ruleForm.id = ''
      this.ruleForm.userType = ''  // 弹窗select
      this.ruleForm.userName = ''
      this.ruleForm.nickName = ''
      this.ruleForm.phone = ''
      this.ruleForm.email = ''
      this.ruleForm.isAnswerer = ''
      this.ruleForm.status = ''
      this.ruleForm.ndate = ''
      this.ruleForm.content = ''
      this.ruleForm.company = ''
      this.ruleForm.job = ''
      this.ruleForm.remark = ''
      this.ruleForm.details = ''
    },
    fn_check(isAudit) {
      var _this = this
      var id = this.ruleForm.id
      this.post("json/isAudit", {
        id,
        isAudit
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false

      });
    },
    excel() {
      window.location.href = 'http://int.answer.updrv.com/json/excel'
    }
  },
  mounted() {
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style scoped lang="scss">
.w-clear:after {
  display: block;
  content: '';
  clear: both;
}

.mb15 {
  margin-bottom: 15px;
}

.f-right {
  float: right;
}

.dialog-centern-box {
  // width: 200px;
  .dialog-top {
    float: left;
    width: 100%;
    .dialog-left {
      float: left;
      width: 50%;
      margin-right: 5%;
    }
    ;
    .dialog-right {
      float: left;
      width: 45%;
    }
    ;
    .dialog-left {
      .user-id-box {
        float: left;
        width: 35%;
        margin-right: 5%;
      }
      ;
      .user-name-box {
        float: left;
        width: 60%;
      }
    }
  }
}
</style>



