<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:300px;float:left;" @keyup.enter="search">
            <el-input v-model="params.keyword" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" :row-key='tableData.id' border tooltip-effect="dark" style="width: 100%" :max-height='mainHeight-132'>
              <el-table-column prop="id" label="ID" width="120"></el-table-column>
              <el-table-column prop="userName" label="用户名"></el-table-column>
              <el-table-column prop="nickName" label="昵称"></el-table-column>
              <el-table-column prop="phone" label="手机号"></el-table-column>
              <el-table-column prop="email" label="邮箱" min-width="150px"></el-table-column>
              <el-table-column label="用户类型">
                <template slot-scope="props">
                  {{ userType[props.row.userType]}}
                </template>
              </el-table-column>
              <el-table-column label="是否答主" width="90">
                <template slot-scope="props">
                  {{ props.row.isAnswerer == 1? "是" : "否" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="注册时间" width="250">
              </el-table-column>
              <el-table-column prop="backCount" label="违规次数">
              </el-table-column>
              <el-table-column label="操作" width="100" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='text' @click='batchTransfer(props.row)'>转入白名单</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="getListAjax" :current-page.sync="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize" layout="total, sizes, prev, pager, next" :total="params.total">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      params:{
        pageSize:10,
        pageNum:1,
        keyword:'',
        total:0
      },
      tableData: []
    }
  },
  methods: {
    search(){
      if(this.params.pageNum != 1){
        this.params.pageNum = 1;
      }
      this.getListAjax();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.params.pageNum = 1;
      this.getListAjax();
    },
    batchTransfer(row) {
      if(!row || !row.id) return;
      this.$confirm('是否将选中用户转入白名单?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.post("/json/whiteuserList",{uid:row.id},(data)=>{
            if(!!data && data.status){
              this.getListAjax();
            }
        });
      })
    },
    getListAjax(){
      this.post("/json/backuserList",this.params,(data)=>{
        if(!!data.data){
          this.tableData = data.data;
          this.params.total = data.totalPage;
        }
      });
    }
  },
  created(){
      this.getListAjax();
  }
}
</script>


