<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
         <div style="width:300px;float:left;" @keyup.enter="search">
            <el-input v-model.trime="params.keyword" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <template>
            <el-select v-model="params.status" placeholder="筛选" style='float:left; margin-left:50px;width:120px' @change='getListAjax()'>
              <el-option label="全部" :value="2"></el-option>
              <el-option label="启用" :value="1"></el-option>
              <el-option label="禁用" :value="0"></el-option>
            </el-select>
          </template>
          <el-button type="primary" @click="openEditDialog()" style="float:right;">新增信息</el-button>
        </div>
        <div class="table">
          <template>
            <el-table :data="tableData" :row-key='tableData.questionID' border tooltip-effect="dark" style="width: 100%" :max-height='mainHeight-132'>
              <el-table-column prop="id" label="ID" width="120"></el-table-column>
              <el-table-column prop="title" label="敏感词" min-width="300"></el-table-column>
              <el-table-column label="状态">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.status == 1)' size='small'>启用</el-tag>
                  <el-tag type="danger" v-else size='small'>禁用</el-tag>
                </template>
              </el-table-column>
               <el-table-column prop="acount" label="触发次数"></el-table-column>
              <el-table-column label="操作" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size="mini" @click="openEditDialog(props.row)" type='primary'>编辑</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="getListAjax" :current-page.sync="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize" layout="total, sizes, prev, pager, next" :total="params.total">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="showTitle" :visible.sync="dialogVisible"  width="500px" top="30vh" style='text-align:left;' :close-on-click-modal='false' @close="close">
        <el-form ref="formData" :model="formData" label-width="80px" :rules="rules"> 
          <el-form-item label="敏感字词" prop = 'title'>
            <el-input v-model="formData.title"></el-input>
          </el-form-item>
          <el-form-item label="状态" prop="status">
            <el-switch v-model="formData.status" :active-value='1' :inactive-value='0'></el-switch>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="save" :loading="saving">保 存</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
  },
  data() {
    return {
      dialogVisible: false,//弹窗显示
      saving:false,
      multipleSelection: [], //选择到的数据
      tableData: [],
      formData:{},
      params:{
        pageSize:10,
        pageNum:1,
        keyword:'',
        status:2,
        total:0
      },
      rules:{
        title:[
          { required: true, message: '请输入敏感字词', trigger: 'blur' },
        ]
      }
    }
  },
  computed:{
    showTitle(){
      return this.isEdit ? '编辑': '新增';
    }
  },
  methods: {
    search(){
      if(this.params.currentPage != 1){
        this.params.currentPage = 1;
      }
      this.getListAjax();
    },
    close(){
      this.$refs['formData'].resetFields();
    },
    openEditDialog(row){
      let addVal = {status:1,title:'',id:0,date:''}
      this.dialogVisible = true;
      !!row ? Object.assign(addVal,row) : addVal;
      this.formData = addVal;
    },
    handleSizeChange(val) {
      this.params.pagesize = val;
      this.params.currentPage = 1;
      this.getListAjax();
    },
    save(){
      this.$refs['formData'].validate((valid)=>{
        if(!valid) return false;
        this.saving = true;
        this.post("/json/addword",this.formData,(data)=>{
          if(!!data && !!data.status){
            this.dialogVisible = false;
            this.getListAjax();
          }
            this.saving = false;
        },()=>{
          this.saving = false;
        });  
      })
    },
    getListAjax(){
     this.post("/json/getlist",this.params,(data)=>{
        if(!!data.data){
          this.tableData = data.data;
          this.params.total = data.totalPage;
        }
      });
    }
  },
  created(){
    this.getListAjax();
  }
}
</script>


