<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:300px;float:left;" @keyup.enter="search">
            <el-input v-model="params.keyWords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <template>
            <el-select v-model="params.status" placeholder="筛选" style='float:left; margin-left:50px;width:120px' @change='getListAjax()'>
              <el-option label="全部" :value="5"></el-option>
              <el-option label="回复" :value="0"></el-option>
              <el-option label="问题" :value="1"></el-option>
              <el-option label="企业用户" :value="2"></el-option>
              <el-option label="个人用户" :value="3"></el-option>
              <el-option label="答主" :value="4"></el-option>
            </el-select>
          </template>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" :row-key='tableData.id' border tooltip-effect="dark" style="width: 100%" :max-height='mainHeight-132'>
              <el-table-column prop="id" label="ID" width="80"></el-table-column>
              <el-table-column label="内容类型" width="150">
                 <template slot-scope="props">
                  {{ props.row.type == 0? "回复" : "问题" }}
                </template>
              </el-table-column>
              <el-table-column prop="title" label="内容标题" min-width="100"></el-table-column>
              <el-table-column prop="status" label="状态" min-width="70">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else size='small'>驳回</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="userName" label="用户名"></el-table-column>
              <el-table-column prop="nickName" label="昵称"></el-table-column>
              <el-table-column prop="phone" label="手机号" width="110"></el-table-column>
              <el-table-column prop="email" label="邮箱" min-width="100"></el-table-column>
              <el-table-column label="用户类型" width="80">
                <template slot-scope="props">
                  {{ userType[props.row.userType]}}
                </template>
              </el-table-column>
              <el-table-column label="是否答主" width="80">
                <template slot-scope="props">
                  {{ props.row.isAnswerer == 1? "是" : "否" }}
                </template>
              </el-table-column>
              <el-table-column prop="backCount" label="违规次数" width="80">
              </el-table-column>
              <el-table-column label="账号状态" width="80">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.forbidden==1)' size='small'>启用</el-tag>
                  <el-tag type="danger" v-else size='small'>禁用</el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="addTime" label="创建时间" min-width="100">
              </el-table-column>
              <el-table-column label="操作" width="140" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='primary' @click='openEditDialog(props.row)'>审核</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="getListAjax" :current-page.sync="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize" layout="total, sizes, prev, pager, next" :total="params.total">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog title="审核" :visible.sync="dialogVisible" width="800px" style='text-align:left;' @close='dialogVisible = false' :close-on-click-modal='false'>
         <el-form :model="formData" label-width="85px">
          <el-row>
            <el-col :span="6">
              <el-form-item label="用户姓名：">
                <span>{{formData.userName}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="用户类型：">
                <el-tag type="success" size='small'>{{ userType[formData.userType]}}</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="答主：">
                <el-tag type="success" v-if='(formData.isAnswerer==1)' size='small'>是</el-tag>
                <el-tag type="danger" v-else size='small'>否</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="账号状态：">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(formData.forbidden==1)' size='small'>启用</el-tag>
                  <el-tag type="danger" v-else size='small'>禁用</el-tag>
                </template>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="内容类型：">
                <template slot-scope="props">
                  <el-tag type="danger" v-if='(formData.type==1)' size='small'>问题</el-tag>
                  <el-tag type="success" v-else size='small'>回复</el-tag>
                </template>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="创建时间：" >
                <el-tag type="success" size='small'>{{formData.responseTime || '2018-12-11'}}</el-tag>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="问题标题：">
                <span>{{formData.title}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="问题详情：">
                <span>{{formData.content}}</span>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="success" @click="audit(1)">通过</el-button>
            <el-button type="danger" @click="audit(0)">拒绝</el-button>
            <el-button type="info" @click="audit(2)">拉黑</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      dialogVisible: false,
      tableData: [],//原始数据
      formData:{}, //弹窗数据
      auditData:{},
      params:{
        pageSize:10,
        pageNum:1,
        keywords:'',
        total:0,
        status : 5
      }
    }
  },
  methods: {
    search(){
      if(this.params.currentPage != 1){
        this.params.currentPage = 1;
      }
      this.getListAjax();
    },
    handleSizeChange(val) {
      this.params.pagesize = val;
      this.params.currentPage = 1;
      this.getListAjax();
    },
    openEditDialog(row) {
      if(!row) return;
      this.dialogVisible = true;
      this.formData = row;
    },
    audit(type){
      this.auditData = this.formData;
      if(type != 2){
        let params = {id:this.auditData.id,status:type};
        this.post("/json/maudit",params,(data)=>{
          if(!!data && data.status){
            this.dialogVisible = false;
            this.getListAjax();
          }
        });
      }else{
        let params = {rids:this.auditData.rid,type:this.auditData.type};
        this.post("/json/maddblack",params,(data)=>{
          if(!!data && data.status){
            this.dialogVisible = false;
            this.getListAjax();
          }
        });
      }
    },
    getListAjax(){
      this.post("/json/auditwordList",this.params,(data)=>{
        if(!!data.data){
          this.tableData = data.data;
          this.params.total = data.totalPage;
        }
      });
    },
  },
  created(){
    this.getListAjax();
  }
}
</script>


