<template>
  <el-container>
    <el-header>
      <div style="float:left;display:flex;align-items:center;">
        <img src="static/images/logo.png" style='width:50px;height:50px;display:block'/>
        <span style='color:#fff;font-size:18px;font-weight:bold;display:block;padding-left:10px;'>证金快答专业答主后台</span>
      </div>
      <div style="float:right;color:#fff">
        <span>
          欢迎
          <span>{{Name}}</span>
          ！
        </span>
        <el-button type="primary" size="medium" @click="exit" plain>退出系统</el-button>
      </div>
    </el-header>
    <el-container style='min-width:1200px;'>
      <app-nav :isAdmin = 'isAdmin'>
      </app-nav>
      <el-main>
        <router-view :mainHeight='mainHeight' :userType = 'userType'></router-view>
      </el-main>
    </el-container>
</el-container>
</template>
<script>
import AppNav from './nav'
export default {
  name: 'app',
  data(){
     return{
      Name:'',
      isAdmin:2,
      mainHeight: '',
      userType:['游客','个人用户','企业用户','个人答主','专业答主','管理员','超级管理员']
    }
  },
  components: { AppNav },
  methods: {
    //退出
    exit() {
      this.$confirm('您即将退出系统, 是否继续?', '提示', {
        confirmButtonText: '安全退出',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.clearAllCookie();
        this.$router.push('/')
      })
    },
   getMianHeight(){
      let offsetH = 60;/*头部*/
      let mainHeight = window.innerHeight - offsetH;
      this.mainHeight = mainHeight < 400 ? 400 : mainHeight;
      window.onresize = (e) =>{
        let mainHeight = window.innerHeight - offsetH;
        this.mainHeight = mainHeight < 540 ? 540 : mainHeight;  //最小的高度是600
      }
    },
    checkLogin(){
      this.post('/json/islogin',{},(data)=>{
        if(data && data.status == 1){
          this.getMianHeight();
          this.getRole();
          this.Name = sessionStorage.getItem('name');
        }
      })
    },
    getRole(){
      var arr = [];
      this.post("/json/role/power", {
      }, (data) => {
        if (data.status == 1) {
          for (var i = 0, y = data.data.length; i < y; i++) {
            arr.push(JSON.stringify(data.data[i]))
          }
          var str = ''
          for (var i = 0; i < arr.length; i++) {
            if (i < arr.length - 1) {
              str = str + arr[i] + '$'
            } else {
              str = str + arr[i]
            }

          }
          sessionStorage.setItem('limits',str);
        }
      })
    },
    clearAllCookie() {  
        let keys = document.cookie.match(/[^ =;]+(?=\=)/g);  
        if(keys) {  
            for(let i = keys.length; i--;)  
                document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()  
        }  
    }  
  },
  created(){
    this.checkLogin();
    if(sessionStorage.getItem('role')){
        this.isAdmin = sessionStorage.getItem('role');
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.w-clear:after {
  display: block;
  content: '';
  clear: both;
}

.f-left {
  float: left;
}

.f-left-right20 {
  float: left;
  margin-right: 20px;
}

.f-left-right50 {
  float: left;
  margin-right: 50px;
}

.mb15 {
  margin-bottom: 15px;
}
</style>


