import Vue from 'vue';
import qs from 'qs';

let vue = new Vue();
let timeout = 20000; //设置超时时间
let post = function (url, data, success, error) {
  let xmlhttp = null;
  let time = false;
  if (window.XMLHttpRequest) {
    xmlhttp = new XMLHttpRequest();
  } else if (window.ActiveXObject) {
    xmlhttp = new ActiveXObject("Microsoft.XMLHTTP");
  }
  if (xmlhttp != null) {
    xmlhttp.open("POST",url, true);
    xmlhttp.setRequestHeader("Content-Type", "application/x-www-form-urlencoded;charset=UTF-8");
    xmlhttp.setRequestHeader('Cache-Control', 'no-cache'); //禁止缓存
    xmlhttp.send(qs.stringify(data));
  }
  let timer = setTimeout(() => {
    time = true;
    xmlhttp.abort();
  }, timeout);
  xmlhttp.onreadystatechange = function () {
    if (time) {
      vue.$message.error('请求超时！');
      clearTimeout(timer);
    }
    if (xmlhttp.readyState == 4) {
      if (xmlhttp.status >= 200 && xmlhttp.status <= 300) {
        let reptext = {};
        if(xmlhttp.responseText == 'EVm703WvfbpiNkaVljJGiw=='){
          reptext = {status:1,msg:'操作成功'};
        }else{
          reptext = JSON.parse(xmlhttp.responseText);
        }
        if (!!reptext.msg) {
          if (reptext.status == 1) {
            vue.$message.success(reptext.msg);
          } else if (reptext.status == -1) {
            location.href = '';
          } else {
            vue.$message.error(reptext.msg);
          }
        }
        success && success(reptext)
      } else {
        vue.$message.error('请求失败！');
        error && error();
      }
    }
  }
  // xmlhttp.timeout = 1000;
  // xmlhttp.ontimeout = function(){
  //     vue.$message.error('请求超时！');
  // }
}
Vue.prototype.post = post;



