<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:300px;float:left;" @keyup.enter="search">
            <el-input v-model="params.keyword" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" :row-key='tableData.id' border tooltip-effect="dark" style="width: 100%" :max-height='mainHeight-132'>
              <el-table-column prop="id" label="清单ID"></el-table-column>
              <el-table-column prop="qid" label="关联问题的ID"></el-table-column>
              <el-table-column prop="title" label="问题标题"></el-table-column>
              <el-table-column label="问题描述">
                 <template slot-scope="props">
                  <div :title='props.row.rescontent' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.rescontent}}</div>
                </template>
              </el-table-column>
              <el-table-column label="是否回复" width="90">
                <template slot-scope="props">
                  {{ props.row.ishuifu == 1? "是" : "-" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="回复时间">
              </el-table-column>
              <el-table-column prop="ulike" label="用户是否点赞">
              </el-table-column>
              <el-table-column prop="status" label="回复状态">
              </el-table-column>
              <el-table-column label="操作" width="120" class-name='noneborder'>
                <template slot-scope="props">
                    <el-button size="mini" @click="showDialog(props.row,1)" type="primary" v-if='!props.row.ishuifu'>回复</el-button>
                    <el-button @click="showDialog(props.row,0)" size="mini" type="info" v-else>查看</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="getListAjax" :current-page.sync="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize" layout="total, sizes, prev, pager, next" :total="params.total">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="title" :visible.sync="dialogVisible" width="700px" top="30vh" style='text-align:left' :close-on-click-modal='false'>
        <el-form ref='formData' :model="formData" label-width="100px" :rules='rules'>
          <el-row>
            <el-col :span="6">
              <el-form-item label="清单ID：">
                <el-tag type="info" size='small'>{{formData.id}}</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
               <el-form-item label="问题ID：">
                <el-tag type="info" size='small'>{{formData.qid}}</el-tag>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
               <el-form-item label="内容标题：" >
                <span>{{formData.title}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="问题描述：">
                <el-input type="textarea" :rows="5" placeholder="请输入回复内容"v-model="formData.rescontent" readonly>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="回复内容：" prop="content">
                <el-input type="textarea" :rows="3" placeholder="请输入回复内容"v-model="formData.content" :disabled='!isWatch'>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
               <el-form-item label="是否回复：">
                <el-tag type="success" size='small' v-if='formData.ishuifu == 1'>是</el-tag>
                <el-tag type="danger" size='small' v-else>-</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
               <el-form-item label="是否点赞：">
                 <el-tag type="success" size='small' v-if='formData.ulike == 1'>是</el-tag>
                  <el-tag type="danger" size='small' v-else>-</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
               <el-form-item label="回复时间：">
                 <el-tag type="success" size='small'>{{formData.ndate}}</el-tag>
              </el-form-item>
            </el-col>
            <el-col :span="6">
               <el-form-item label="回复状态：">
                <el-tag type="success" size='small' v-if='formData.status != "-"'>{{formData.status}}</el-tag>
                <el-tag type="danger" size='small' v-else>-</el-tag>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="save" :loading="saving" v-if='isWatch'>提交</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String]
  },
  data() {
    return {
      params:{
        pageSize:10,
        pageNum:1,
        keyword:'',
        total:0
      },
      tableData: [],
      dialogVisible:false,
      isWatch:false,
      title:'',
      saving:false,
      formData:{},
      rules:{
        content:[
          { required: true, message: '请输入回复内容', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    search(){
      if(this.params.pageNum != 1){
        this.params.pageNum = 1;
      }
      this.getListAjax();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.params.pageNum = 1;
      this.getListAjax();
    },
    save(){
      this.$refs.formData.validate((valid)=>{
        if(!valid) return;
        this.saving = true;
        let params = {cmd:'askComment',userID:this.formData.userID,qID:this.formData.qid,isAnonymous:1,content:this.formData.content};
        this.post("/api/v1",params,(data)=>{
          if(data && data.status == 1){
            this.dialogVisible = false;
            this.getListAjax();
          }
          this.saving = false;
        });
      })
    },
    showDialog(row,type){
      if(!row) return;
      this.$refs.formData && this.$refs.formData.resetFields();
      this.dialogVisible = true;
      this.formData = row;
      if(type){
        this.isWatch = true;
        this.title = '回复信息'
      }else{
        this.isWatch = false;
        this.title = '查看信息'
      }
    },
    getListAjax(){
      this.params.userID = 34;
      this.post("/json/tasklist",this.params,(data)=>{
        if(!!data.data){
          this.tableData = data.data;
          this.params.total = data.totalPage;
        }
      });
    }
  },
  created(){
      this.getListAjax();
  }
}
</script>


