<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <div style="width:600px;float:left;margin-left:100px;">
            <el-button type="primary">新增</el-button>
            <el-button type="danger">删除</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="questionID" label="问题ID" width="120">
              </el-table-column>
              <el-table-column prop="quizName" label="提问人" width="120">
              </el-table-column>
              <el-table-column prop="whetherAnonymity" label="是否匿名" width="80">
              </el-table-column>
              <el-table-column prop="questionTitle" label="问题标题" min-width="300">
              </el-table-column>
              <el-table-column prop="questionParticulars" label="问题详情" min-width="300">
              </el-table-column>
              <el-table-column prop="replySum" label="回复数">
              </el-table-column>
              <el-table-column prop="attentionSum" label="关注数">
              </el-table-column>
              <el-table-column prop="questionFoundTime" label="问题创建时间">
              </el-table-column>
              <el-table-column label="操作" width="90" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='text' @click='openEditDialog(props.row)'>删除</el-button>
                </template>
              </el-table-column>

            </el-table>
          </template>
        </div>
      </div>
    </el-main>

  </el-container>
</template>
<script>
export default {
  data() {
    return {
      tableData: [{
        questionID: 100001,
        quizName: '张三丰',
        whetherAnonymity: 1,
        questionTitle: '啦啦啦啦啦啦啦啦啦啦啦啦啦？',
        questionParticulars: '啦啦啦啦啦啦啦啦啦啦啦啦啦？ABCD',
        replySum: 10,
        attentionSum: 5,
        questionFoundTime: '2018-02-22'
      }],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: '是否朱主答'
      }, {
        value: '1',
        label: '启用状态'
      }, {
        value: '2',
        label: '注册时间'
      }],
      value: ''

    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    fn_href(str) {
      window.location.href = str;
    }
  }
}
</script>


