<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()">搜索</el-button>
          </div>
          <div style="width:120px;float:right;">
            <el-button type="primary" @click="centerDialogVisible = true,dialogTitle='新增信息(测试专用上线取消)'">新增信息</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="提问ID" width="120">
              </el-table-column>
              <el-table-column prop="userID" label="企业用户ID" width="120">
              </el-table-column>
              <el-table-column prop="userName" label="企业用户名" width="120">
              </el-table-column>
              <el-table-column label="问题标题" width="350">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row2" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column label="问题详情">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row2" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column label="是否回复" width="80">
                <template slot-scope="props">
                  {{ props.row.ishuifu == 0? "未回复" : "已回复" }}
                </template>
              </el-table-column>
              <el-table-column prop="answerName" label="问题回复专家ID" width="120">
              </el-table-column>
              <el-table-column label="是否点赞专家回复" width="140">
                <template slot-scope="props">
                  {{ props.row.islike == 0? "否" : "是" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="问题创建时间" width="120">
              </el-table-column>
              <el-table-column label="问题状态" width="100">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="warning" v-if='(props.row.isallot==0)' size='small'>未分配</el-tag>
                  <el-tag type="success" v-else size='small'>已分配</el-tag>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="110" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='primary' @click="openEditDialog(props.row),dialogTitle='分配问题'">分配问题</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:150px;">问题ID：{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">企业用户ID：</div>
              <div class="f-left">
                <el-input type="text" v-model="userID" size="mini" placeholder="请输入内容"></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">企业用户名：</div>
              <div class="f-left">
                <el-input type="text" v-model="userName" size="mini" placeholder="请输入内容"></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              问题标题：
            </div>
            <div class="f-left" style="width:870px;">
              <el-input type="text" v-model="title" placeholder="请输入内容" size="mini"></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div>问题描述：</div>
            <div>
              <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="content"></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right20">
              <div class="f-left">
                是否回复：
              </div>
              <div class="f-left">
                <template>
                  <el-radio v-model="ishuifu" label="1">是</el-radio>
                  <el-radio v-model="ishuifu" label="2">否</el-radio>
                </template>
              </div>
            </div>

            <div class="f-left-right20">
              <div class="f-left">
                回复问题专家ID：
              </div>
              <div class="f-left">
                <template>
                  <el-select v-model="value2" size="mini" placeholder="请选择">
                    <el-option v-for="item in options2" :key="item.userID" :label="item.userName" :value="item.userID">
                    </el-option>
                  </el-select>
                </template>
              </div>
            </div>
            <div class="f-left-right20">
              <div class="f-left">
                是否点赞专家回复 ：
              </div>
              <div class="f-left">
                <template>
                  <el-radio v-model="islike" label="1">是</el-radio>
                  <el-radio v-model="islike" label="0">否</el-radio>
                </template>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left">问题创建时间：</div>
              <div class="f-left">
                <div class="block">
                  <el-date-picker v-model="ndate" align="right" type="date" size="mini" placeholder="选择日期">
                  </el-date-picker>
                </div>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left">
                问题状态：
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="info" v-if='(isallot == 0)' size='medium'>未分配</el-tag>
                  <el-tag type="success" v-else size='medium'>已分配</el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="v">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="fn_add()">确 定</el-button>
          </div>
          <div v-if="!v">
            <el-button @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="fn_allot()">分 配</el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      v: true,
      Url: 'http://int.answer.updrv.com',
      dialogTitle: '新增信息',
      currentPage: 1, // 当前页
      maxPage: 100,   // 总页数
      pageArr: 10, // 显示行数
      initial: 20, // 初始显示行数
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: '未回复的问题'
      }, {
        value: '1',
        label: '已回复的问题'
      }],
      options2: [],
      value: '',
      value2: '',
      id: '', //问题id
      userID: '', //企业用户id
      userName: '', //企业用户名
      title: '', //问题标题
      content: '', //内容详情
      ishuifu: '', //是否回复  0未回复 1 已回复
      answerName: '', //回复问题专家id (专家id-专家名称)
      islike: '', //是否点赞专家回复  0 否 1是
      ndate: '', //问题创建时间
      isallot: 0  //问题状态  0未分配 1已分配
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
      this.centerDialogVisible = true
      var val = ''
      for (var i = 0; i < this.options2.length; i++) {

        if (this.options2[i].userName == row.answerName) {
          val = this.options2[i].userID
        }

      }
      this.v = false
      // this.value2 = row.answerName.split('-')[0]
      this.value2 = row.answerName
      this.id = row.id //问题id
      this.userID = row.userID //企业用户id
      this.userName = row.userName //企业用户名
      this.title = row.title //问题标题
      this.content = row.content //内容详情
      this.ishuifu = row.ishuifu + ''//是否回复  0未回复 1 已回复
      this.answerName = row.answerName //回复问题专家id (专家id-专家名称)
      this.islike = row.islike + '' //是否点赞专家回复  0 否 1是
      this.ndate = row.ndate //问题创建时间
      this.isallot = row.isallot  //问题状态  0未分配 1已分配
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.v = true
      this.value2 = ''
      this.id = '' //问题id
      this.userID = '' //企业用户id
      this.userName = '' //企业用户名
      this.title = '' //问题标题
      this.content = '' //内容详情
      this.ishuifu = '' //是否回复  0未回复 1 已回复
      this.answerName = '' //回复问题专家id (专家id-专家名称)
      this.islike = '' //是否点赞专家回复  0 否 1是
      this.ndate = '' //问题创建时间
      this.isallot = 0  //问题状态  0未分配 1已分配
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将永久删除所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量删除功能喔~！'
        });
      }
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/companyList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      // console.log(val)
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/questAdd", {
        userName, // 用户名
        title, // 问题标题
        content, // 问题描述
        ndate // 时间
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_allot() {
      var _this = this
      var qid = this.id, uid = this.value2
      this.post("/json/company/answerfenpei", {
        qid,
        uid
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_postion() {
      var _this = this
      this.post("/json/company/answerList", {
      }, (data) => {
        _this.options2 = data.data
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
    this.fn_postion()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


