<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="请输入内容"></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()">搜索</el-button>
          </div>
          <div style="width:340px;float:right;">
            <el-button type="primary" @click="centerDialogVisible = true,dialogTiele='新增信息(测试专用上线取消)'">新增信息</el-button>
            <el-button type="danger" @click="fn_repulse">批量拒绝</el-button>
            <el-button type="info" @click="fn_blacklist">批量拉黑</el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange"
            :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="回复ID" width="80">
              </el-table-column>
              <el-table-column prop="qID" label="关联问题ID" width="100">
              </el-table-column>
              <el-table-column label="问题标题">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="userID" label="答主ID" width="160">
              </el-table-column>
              <el-table-column prop="userName" label="答主用户名" width="160">
              </el-table-column>
              <el-table-column label="是否匿名回复" width="120">
                <template slot-scope="props">
                  {{props.row.isAnonymous==0?"是":"否"}}
                </template>
              </el-table-column>
              <el-table-column label="回复详情">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="viewCount" label="查看人数" width="80">
              </el-table-column>
              <el-table-column prop="userlikeCount" label="点赞人数" width="80">
              </el-table-column>
              <el-table-column prop="userIDlike" label="点赞用户ID" width="100">
              </el-table-column>
              <el-table-column prop="ndate" label="回复时间" width="100">
              </el-table-column>
              <el-table-column label="回复状态" width="80">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "男" : "女" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else size='small'>驳回</el-tag>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary">审核</el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info">详情</el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:150px;">回复ID：{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">关联问题ID：</div>
              <div class="f-left">
                <el-input v-model="qID" size="mini" placeholder="请输入内容" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">问题标题：</div>
              <div class="f-left" style="width:300px;">
                <el-input v-model="title" size="mini" placeholder="请输入标题" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left">
                答主ID：
              </div>
              <div class="f-left">
                <el-input v-model="userID" placeholder="请输入内容" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">
                答主用户名：
              </div>
              <div class="f-left" style="width:260px;">
                <el-input v-model="userName" placeholder="请输入内容" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">
                是否匿名：
              </div>
              <div class="f-left">
                <el-radio v-model="isAnonymous" label="0" :disabled='zhiDu'>是</el-radio>
                <el-radio v-model="isAnonymous" label="1" :disabled='zhiDu'>否</el-radio>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div>
              回复详情：
            </div>
            <div>
              <el-input type="textarea" v-model="content" :rows="3" placeholder="请输入内容" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              查看人数：{{viewCount}}
            </div>
            <div class="f-left-right50">
              回复人数：{{userlikeCount}}
            </div>
          </div>
          <div class="w-clear mb15">
            <div>
              点赞用户ID：
            </div>
            <div>
              <el-input type="textarea" v-model="userIDlike" :rows="3" placeholder="请输入内容" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:620px;">
              <div class="f-left">注册时间：</div>
              <div class="block f-left">
                <el-date-picker v-model="ndate" align="right" type="date" placeholder="选择日期" size="mini" value-format="yyyy-MM-dd" :disabled="zhiDu">
                </el-date-picker>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">
                回复状态：
              </div>
              <div style="float:left;">
                <template>
                  <el-tag type="success" v-if='(status==1)' size='small'>通过</el-tag>
                  <el-tag type="danger" v-else size='small'>驳回</el-tag>
                </template>

              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="add">
            <el-button type="" @click="centerDialogVisible = false">取 消</el-button>
            <el-button v-if="add2" type="primary" @click=" fn_add()">确 定</el-button>
          </div>
          <div v-if="!add">
            <el-button type="" @click="centerDialogVisible = false">取 消</el-button>
            <el-button type="info" @click=" fn_black()">拉 黑</el-button>
            <el-button type="danger" @click="fn_check_to(0)">拒 绝</el-button>
            <el-button type="primary" @click="fn_check_to(1)">通 过</el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>

  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      add: true,
      add2: true,
      zhiDu: false,
      dialogTitle: '新增信息',
      currentPage: 1, // 当前页
      maxPage: 100,   // 总页数
      pageArr: 10, // 显示行数
      initial: 20, // 初始显示行数
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: '通过的问题'
      }, {
        value: '1',
        label: '驳回的问题'
      }],
      value: '',
      id: '',  //回复id
      qID: '', //关联问题id
      title: '', //问题标题
      userID: '',     //答主id
      userName: '', //答主用户名
      isAnonymous: '', //是否匿名回复  0 匿名  1不匿名
      content: '', //回复详情
      viewCount: '',  //查看人数
      userlikeCount: '',//点赞人数
      userIDlike: '', //点赞用户id
      ndate: '', //回复时间
      status: 1  //回复状态   0 驳回 1 通过

    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.zhiDu = false
      this.add = true
      this.add2 = true
      this.id = ''  //回复id
      this.qID = '' //关联问题id
      this.title = '' //问题标题
      this.userID = ''     //答主id
      this.userName = '' //答主用户名
      this.isAnonymous = '' //是否匿名回复  0 匿名  1不匿名
      this.content = '' //回复详情
      this.viewCount = ''  //查看人数
      this.userlikeCount = ''//点赞人数
      this.userIDlike = '' //点赞用户id
      this.ndate = '' //回复时间
      this.status = 1  //回复状态   0 驳回 1 通过
    },
    fn_repulse() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将拒绝审核所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/answerAuditlist", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消拒绝'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量功能喔~！'
        });
      }
    },
    fn_blacklist() {
      if (this.multipleSelection.length > 0) {
        this.$confirm('此操作将拉黑所选文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/backanswerList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消拉黑'
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '请在条信息前面打钩之后才能使用批量功能喔~！'
        });
      }
    },
    fn_check(data) {
      this.add = false
      this.centerDialogVisible = true
      this.dialogTitle = '审核信息'
      console.log(data)
      this.id = data.id  //回复id
      this.qID = data.qID //关联问题id
      this.title = data.title //问题标题
      this.userID = data.userID     //答主id
      this.userName = data.userName //答主用户名
      this.isAnonymous = data.isAnonymous + '' //是否匿名回复  0 匿名  1不匿名
      this.content = data.content //回复详情
      this.viewCount = data.viewCount  //查看人数
      this.userlikeCount = data.userlikeCount//点赞人数
      this.userIDlike = data.userIDlike //点赞用户id
      this.ndate = data.ndate //回复时间
      this.status = data.status  //回复状态   0 驳回 1 通过
    },
    fn_look(data) {
      this.dialogTitle = '查看信息'
      this.zhiDu = true
      this.add2 = false
      this.centerDialogVisible = true
      console.log(data)
      this.id = data.id  //回复id
      this.qID = data.qID //关联问题id
      this.title = data.title //问题标题
      this.userID = data.userID     //答主id
      this.userName = data.userName //答主用户名
      this.isAnonymous = data.isAnonymous +'' //是否匿名回复  0 匿名  1不匿名
      this.content = data.content //回复详情
      this.viewCount = data.viewCount  //查看人数
      this.userlikeCount = data.userlikeCount//点赞人数
      this.userIDlike = data.userIDlike //点赞用户id
      this.ndate = data.ndate //回复时间
      this.status = data.status  //回复状态   0 驳回 1 通过
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/answerAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_black() {
      var _this = this
      var id = this.id
      this.post("/json/backanswer", {
        id
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/answerList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      var title = this.title, userName = this.userName, content = this.content
      this.post("/json/answerAdd", {
         title,
        userName,
        content
      }, (data) => {
         _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


