<template>
  <el-form :model="params" :rules="rules" ref="ruleForm" class='login-box'>
    <img class="img1" src="static/images/logo.png">
    <div id="title">问答APP后台</div>
    <el-form-item prop="userName">
      <el-input type="text" v-model.trim="params.userName" auto-complete='off' placeholder="请输入用户名" @keyup.13.native='login' ref="input"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model.trim="params.password" auto-complete='off' laceholder="请输入密码" @keyup.13.native='login'></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" style="width:100%;" :loading="logining" @click.native.prevent="login">登录</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  data() {
    return {
      isload: false,
      logining: false,
      formData: {},
      params: {
        userName: '',
        password: ''
      },
      rules: {
        userName: [
          { required: true, message: '请输入用户名', trigger: 'change' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    login() {
      if (this.logining) return;
      this.logining = true;
      this.$refs.ruleForm.validate((valid) => {
        if (!valid) { this.logining = false; return; }
        this.post("/json/login", this.params, (data) => {
          if (!!data && !!data.status) {
            sessionStorage.setItem('name', data.username);
            sessionStorage.setItem('role', data.role);
            this.$router.push('/home');
          }
          this.logining = false;
        }, () => {
          this.logining = false;
        });
      })
    }
  },
  mounted() {
    this.$refs['input'].focus()
  }
}
</script>
<style>
html,
body {
  margin: 0;
  padding: 0;
  background: #aaa;
}

.img1 {
  width: 100px;
  height: 100px;
  display: block;
  margin: 0 auto;
}



/*第一种垂直居中的方法*/


/*.login-box { 
  position: fixed;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 400px;
  border: 1px solid #999; 
  border-radius: 5px;
  background: #fff;
  transform: translate(-50%, -60%);
}*/


/*第二种垂直居中的方法*/

.login-box {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 340px;
  height: 340px;
  border: 1px solid #999;
  border-radius: 5px;
  background: #fff;
  padding: 30px;
}


#title {
  font-size: 24px;
  color: red;
  font-weight: 700;
  text-align: center;
  margin: 5px 0;
}
</style>



